<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <router-link to="/home/buyer" class="button button-link button-nav pull-left"><span class="icon icon-left"></span></router-link>
            <h1 class="title"></h1>
        </header>
        <div class="content gray">
            <!--  -->
            <div class="description">1.
                <br> 2.-
                <br> 3.
            </div>
            <div class="main-box">
                <div class="box-bd">
                    <div class="list-block">
                        <ul class="thinForm">
                            <li class="multi">
                                <div class="item-content">
                                    <div class="item-inner lt">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input id="target" v-model="url" type="textarea" :rows="2"></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div class="rightBtnWrap">
                        <a href="javascript:;" id="copyBtn" data-clipboard-action="copy" data-clipboard-target="#target" class="button button-fill"></a>
                    </div>
                </div>
            </div>
            <div class="main-box" v-if="listData.length>0">
                <div class="box-bd">
                    <div class="flow-list-block list-block">
                        <ul>
                            <li class="item-content header">
                                <div class="item-inner">
                                    <div class="item-title"></div>
                                    <div class="item-after"></div>
                                </div>
                            </li>
                            <!-- todo -->
                            <li class="item-content" v-for="item in listData" :key="item.id">
                                <div class="item-inner">
                                    <div class="item-title"></div>
                                    <div class="item-after">+2000</div>
                                </div>
                                <div class="footer">2018-05-05 12:22</div>
                            </li>
                            <!-- todo end -->
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
let $ = window.$;

export default {
    name: 'homeFriend',
    data() {
        return {
            url: '',
            listData: []
        };
    },
    created() {
        this.getUrlFun();
    },
    mounted() {
        this.copyUrlFun();
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-20
         * @lastTime    2018-07-20
         * @description 
         */
        getUrlFun() {
            this.$http.post('/userInvite/add').then((response) => {
                if (response.data.success) {
                    let id = response.data.data.inviteId;
                    this.url = window.location.origin + '/#/account/reg/' + id;
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-20
         * @lastTime    2018-07-20
         * @description 
         */
        copyUrlFun() {
            var clipboard = new window.ClipboardJS('#copyBtn');
            clipboard.on('success', function(e) {
                $.toast('!');
                e.clearSelection();
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-20
         * @lastTime    2018-07-20
         * @description 
         */
        getDataFun() {
            // TODO
        }
    }
};

</script>



